<template>
  <div class="cms-list-layout">
    <cms-prompt>
      <li>可对店铺动态进行编辑、删除等操作；置顶操作将信息以跑马灯的形式循环显示在店铺首页</li>
    </cms-prompt>
    <!-- 筛选区 -->
    <section class="cms-list-query">
      <div class="query-group">
        <label>发布时间</label>
        <cms-picker v-model="queryParams.timer" @change="setTimer"></cms-picker>
      </div>
      <div class="query-group">
      <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
    </section>
    <!-- 操作区 -->
    <section class="cms-batch">
      <cms-button icon="add"  type="success" size="small" @click="$routerLink('/shopDynamic/save','save')">添加动态</cms-button>
      <cms-button icon="error"  type="error" size="small" @click="deleteDatas()">批量删除</cms-button>
    </section>
    <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" @selection-change='getCheckItems' @row-click='checkRows' ref='dataTable'>
      <el-table-column type="selection" width="46" fixed='left'></el-table-column>
      <el-table-column prop="id" label="id" width="60"></el-table-column>
      <el-table-column prop="content" label="公告内容" min-width="160"></el-table-column>
      <el-table-column prop="storeUsername" label="发布时间" min-width="160">
        <template  slot-scope="scope">
           {{$moment(scope.row.createTime)}}
        </template>
      </el-table-column>
      <el-table-column prop="isShow" label="显示" width="110">
        <div slot-scope="scope">
          <cms-switch v-model="scope.row.isShow" :id='scope.row.id'></cms-switch>
        </div>
      </el-table-column>
      <el-table-column label="操作" width='175' fixed="right" align="center">
        <div slot-scope="scope">
            <cms-button icon='edit'   size='small' type='info' @click="$routerLink('/shopDynamic/update','update',{id:scope.row.id})">编辑</cms-button>
          <cms-button icon='error'   size='small' type='info' @click.stop="deleteData(scope.row.id)">删除</cms-button>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
  </div>
</template>
<script>
import list from '@/mixins/list'
import {
  shopDynamicList,
  shopDynamicDelete
} from './../../../../api/shop/shopDynamic.js'
var defaultData = {
  page: 1,
  size: 15
}
export default {
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData)
    }
  },
  methods: {
    deleteData(id) {
      // 包装一层删除的操作
      this.deleteTableData(shopDynamicDelete, id) // 这个才是真实的删除方法
    },
    deleteDatas() {
      this.deleteTableDatas(shopDynamicDelete, this.ids) // 这个才是真实的删除方法
    },
    setTimer(timer) {
      // 设置时间区间
      this.queryParams.startTime = timer[0]
      this.queryParams.endTime = timer[1]
      this.query()
    }
  },
  activated() {
    // 缓存触发
    if (this.$route.query.type === 'list') {
      this.queryParams = Object.assign({}, defaultData)
      // 这个值一定要用动态的变量
      this.getTableData(shopDynamicList, this.queryParams)
    }
  },
  created() {
    // 首次触发
    this.getTableData(shopDynamicList, this.queryParams)
  }
}
</script>

<style>
</style>



